
header {
  position: sticky;
  height: 100px;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  z-index: 1;
  background: #eee;
  box-shadow: 0 5px 5px -2px #ccc;
}
header li {
  display: inline-block;
}
header button {
  height: 50px;
  line-height: 50px;
  background: #ccc;
  border: 1px solid #333;
  border-radius: 3px;
  font-size: 1.2rem;
  padding: 0 10px;
  opacity: 0.7;
  cursor: pointer;
}
header li:first-child button {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: 0;
  margin-right: -4px;
}
header li:last-child button:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
header button.active,
header button:hover {
  background: #96e0ff;
  opacity: 1;
  cursor: default;
}
header button:hover {
  text-decoration: underline;
  cursor: pointer;
}
main {
  padding: 0 20px;
  position: relative;
}
main aside {
  width: 15%;
  min-width: 200px;
  position: sticky;
  top: 100px;
  left: 0;
  height: calc(100vh - 100px);
  float: left;
  overflow-y: scroll;
  padding-top: 20px;
  box-shadow: -5px 0 5px -2px #ccc inset;
}
main > div {
  width: 85%;
  padding-left: 20px;
  padding-top: 20px;
  margin-left: 15%;
}
header h1 {
  display: block;
  width: 359px;
  height: 90px;
  color: transparent;
}
header h1.ultra-sun-moon {
  background: url(26d3d92f0e15bd8289eb531f244c76ed.png);
  background-size: cover;
}
header h1.sun-moon {
  background: url(d68fedc80d1f46ee61e71131b45806ef.png);
  background-size: cover;
}
.back-to-top {
  width: 50px;
  height: 50px;
  background: rgba(102, 102, 102, 0.6);
  color: #fff;
  position: fixed;
  bottom: 50px;
  right: 0;
  display: block;
  text-align: center;
}
.back-to-top svg {
  width: 35px;
  margin-top: 5px;
  height: 35px;
  fill: #fff;
}
